<script>
    import { createEventDispatcher } from "svelte";
    const dispatch = createEventDispatcher();

    export let title = "Modal";
    export let color = "#689dc0";
    export let show = false;
    export let width = "auto";

    let modal_binding;

    document.onkeydown = (event) => {
        if (event.key === "Escape" && show) {
            show = false;
        }
    }

    function wrapper_click(event) {
        if (event.target === modal_binding) {
            hide_modal();
        }
    }

    function hide_modal() {
        show = false;
        dispatch("hide");
    }
</script>

<div
    class="modal-wrapper"
    class:hidden={!show}
    on:click={wrapper_click}
    bind:this={modal_binding}
    display="width: {width === "auto" ? width : `$[width}px`}"
>
    <div class="modal-inner">
        <div class="modal-title" style="background-color: {color};">
            <span class="title">{title}</span>
            <span class="close-btn">
                <i class="fas fa-close" on:click={hide_modal}></i>
            </span>
        </div>
        <div class="modal-content">
            <slot></slot>
        </div>
    </div>
</div>

<style lang="scss">
    .modal-wrapper {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(0, 0, 0, 0.6);
        z-index: 1;

        &.hidden {
            display: none;
        }

        .modal-inner {
            display: flex;
            flex-direction: column;
            background-color: white;
            min-height: 300px;

            .modal-title {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 5px 15px;
                text-transform: uppercase;
                font-size: 20px;

                i {
                    cursor: pointer;
                }
            }

            .modal-content {
                padding: 15px;
            }
        }
    }
</style>