<script>
    export let index = 1;
    export let scale = 1;
    export let color = "#000";
    index = index > 9 ? index : `0${index}`;
</script>

<div class="load-wrapper">
    <div class="scalebox" style="transform: scale({scale}, {scale});">
        <div style="color: {color}" class="loader-{index}"></div>
    </div>
</div>

<style lang="scss">
    .load-wrapper {
        display: flex;
        flex: 1;
        justify-content: center;
        align-items: center;
    }

    [class*="loader-"] {
        display: inline-block;
        width: 1em;
        height: 1em;
        color: inherit;
        vertical-align: middle;
        pointer-events: none;
    }

    .loader-01 {
        border: .2em dotted currentcolor;
        border-radius: 50%;
        animation: 1s loader-01 linear infinite;
    }

    @keyframes loader-01 {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    ////////////////////////////////////////
    .loader-02 {
        border: .2em solid transparent;
        border-left-color: currentcolor;
        border-right-color: currentcolor;
        border-radius: 50%;
        animation: 1s loader-02 linear infinite;
    }

    @keyframes loader-02 {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    /////////////////////////////////////
    .loader-03 {
        border: .2em solid currentcolor;
        border-bottom-color: transparent;
        border-radius: 50%;
        animation: 1s loader-03 linear infinite;
        position: relative;
    }

    @keyframes loader-03 {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    /////////////////////////////////////
    .loader-04 {
        border: 1px solid currentcolor;
        border-radius: 50%;
        animation: 1s loader-04 linear infinite;
        position: relative;
        &:before {
            content: '';
            display: block;
            width: 0;
            height: 0;
            position: absolute;
            top: -.2em;
            left: 50%;
            border: .2em solid currentcolor;
            border-radius: 50%;
        }
    }

    @keyframes loader-04 {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    /////////////////////////////////////
    .loader-05 {
        border: .2em solid transparent;
        border-top-color: currentcolor;
        border-radius: 50%;
        animation: 1s loader-05 linear infinite;
        position: relative;
        &:before {
            content: '';
            display: block;
            width: inherit;
            height: inherit;
            position: absolute;
            top: -.2em;
            ;
            left: -.2em;
            ;
            border: .2em solid currentcolor;
            border-radius: 50%;
            opacity: .5;
        }
    }

    @keyframes loader-05 {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    ////////////////////////////////////////
    .loader-06 {
        border: .2em solid currentcolor;
        border-radius: 50%;
        animation: loader-06 1s ease-out infinite;
    }

    @keyframes loader-06 {
        0% {
            transform: scale(0);
            opacity: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            transform: scale(1);
            opacity: 0;
        }
    }

    ///////////////////////////////////////
    .loader-07 {
        border: 0 solid transparent;
        border-radius: 50%;
        position: relative;
        &:before,
        &:after {
            content: '';
            border: .2em solid currentcolor;
            border-radius: 50%;
            width: inherit;
            height: inherit;
            position: absolute;
            top: 0;
            left: 0;
            animation: loader-07 1s linear infinite;
            opacity: 0;
        }
        &:before {
            animation-delay: 1s;
        }
        &:after {
            animation-delay: .5s;
        }
    }

    @keyframes loader-07 {
        0% {
            transform: scale(0);
            opacity: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            transform: scale(1);
            opacity: 0;
        }
    }

    ///////////////////////////////////////
    .loader-08 {
        position: relative;
        &:before,
        &:after {
            content: '';
            width: inherit;
            height: inherit;
            border-radius: 50%;
            background-color: currentcolor;
            opacity: 0.6;
            position: absolute;
            top: 0;
            left: 0;
            animation: loader-08 2.0s infinite ease-in-out;
        }
        &:after {
            animation-delay: -1.0s;
        }
    }

    @keyframes loader-08 {
        0%,
        100% {
            transform: scale(0.0);
        }
        50% {
            transform: scale(1.0);
        }
    }

    ///////////////////////////////////////////////////
    .loader-09 {
        background-color: currentcolor;
        border-radius: 50%;
        animation: loader-09 1.0s infinite ease-in-out;
    }

    @keyframes loader-09 {
        0% {
            transform: scale(0);
        }
        100% {
            transform: scale(1.0);
            opacity: 0;
        }
    }

    /////////////////////////////////////////////////
    .loader-10 {
        position: relative;
        animation: loader-10-1 2.0s infinite linear;
        &:before,
        &:after {
            content: '';
            width: 0;
            height: 0;
            border: .5em solid currentcolor;
            display: block;
            position: absolute;
            border-radius: 100%;
            animation: loader-10-2 2s infinite ease-in-out;
        }
        &:before {
            top: 0;
            left: 50%;
        }
        &:after {
            bottom: 0;
            right: 50%;
            animation-delay: -1s;
        }
    }

    @keyframes loader-10-1 {
        100% {
            transform: rotate(360deg);
        }
    }

    @keyframes loader-10-2 {
        0%,
        100% {
            transform: scale(0);
        }
        50% {
            transform: scale(1);
        }
    }

    ////////////////////////////////////////////////
    .loader-11 {
        background-color: currentcolor;
        animation: loader-11 1.2s infinite ease-in-out;
    }

    @keyframes loader-11 {
        0% {
            transform: perspective(120px) rotateX(0deg) rotateY(0deg);
        }
        50% {
            transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
        }
        100% {
            transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
        }
    }

    ////////////////////////////////////////////////
    .loader-12 {
        position: relative;
        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            background-color: currentcolor;
            left: 50%;
            right: 0;
            top: 0;
            bottom: 50%;
            box-shadow: -.5em 0 0 currentcolor;
            animation: loader-12 1s linear infinite;
        }
        &:after {
            top: 50%;
            bottom: 0;
            animation-delay: .25s;
        }
    }

    @keyframes loader-12 {
        0%,
        100% {
            box-shadow: -.5em 0 0 transparent;
            background-color: currentcolor;
        }
        50% {
            box-shadow: -.5em 0 0 currentcolor;
            background-color: transparent;
        }
    }

    /////////////////////////////////////////////////
    .loader-13:before,
    .loader-13:after,
    .loader-13 {
        border-radius: 50%;
        animation-fill-mode: both;
        animation: loader-13 1.8s infinite ease-in-out;
    }

    .loader-13 {
        color: currentcolor;
        position: relative;
        transform: translateZ(0);
        animation-delay: -0.16s;
        top: -1em;
        &:before {
            right: 100%;
            animation-delay: -0.32s;
        }
        &:after {
            left: 100%;
        }
        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            width: inherit;
            height: inherit;
        }
    }

    @keyframes loader-13 {
        0%,
        80%,
        100% {
            box-shadow: 0 1em 0 -1em;
        }
        40% {
            box-shadow: 0 1em 0 -.2em;
        }
    }

    //////////////////////////////////////////////////////////
    .loader-14 {
        border-radius: 50%;
        box-shadow: 0 1em 0 -.2em currentcolor;
        position: relative;
        animation: loader-14 0.8s ease-in-out alternate infinite;
        animation-delay: 0.32s;
        top: -1em;
        &:after,
        &:before {
            content: '';
            position: absolute;
            width: inherit;
            height: inherit;
            border-radius: inherit;
            box-shadow: inherit;
            animation: inherit;
        }
        &:before {
            left: -1em;
            animation-delay: 0.48s;
        }
        &:after {
            right: -1em;
            animation-delay: 0.16s;
        }
    }

    @keyframes loader-14 {
        0% {
            box-shadow: 0 2em 0 -.2em currentcolor;
        }
        100% {
            box-shadow: 0 1em 0 -.2em currentcolor;
        }
    }

    ////////////////////////////////////////////
    .loader-15 {
        background: currentcolor;
        position: relative;
        animation: loader-15 1s ease-in-out infinite;
        animation-delay: 0.4s;
        width: .25em;
        height: .5em;
        margin: 0 .5em;
        &:after,
        &:before {
            content: '';
            position: absolute;
            width: inherit;
            height: inherit;
            background: inherit;
            animation: inherit;
        }
        &:before {
            right: .5em;
            animation-delay: 0.2s;
        }
        &:after {
            left: .5em;
            animation-delay: 0.6s;
        }
    }

    @keyframes loader-15 {
        0%,
        100% {
            box-shadow: 0 0 0 currentcolor, 0 0 0 currentcolor;
        }
        50% {
            box-shadow: 0 -.25em 0 currentcolor, 0 .25em 0 currentcolor;
        }
    }

    ////////////////////////////////////////////
    .loader-16 {
        transform: rotateZ(45deg);
        perspective: 1000px;
        border-radius: 50%;
        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: inherit;
            height: inherit;
            border-radius: 50%;
            animation: 1s spin linear infinite;
        }
        &:before {
            transform: rotateX(70deg);
        }
        &:after {
            transform: rotateY(70deg);
            animation-delay: .4s;
        }
    }

    @keyframes rotate {
        0% {
            transform: translate(-50%, -50%) rotateZ(0deg);
        }
        100% {
            transform: translate(-50%, -50%) rotateZ(360deg);
        }
    }

    @keyframes rotateccw {
        0% {
            transform: translate(-50%, -50%) rotate(0deg);
        }
        100% {
            transform: translate(-50%, -50%) rotate(-360deg);
        }
    }

    @keyframes spin {
        0%,
        100% {
            box-shadow: .2em 0px 0 0px currentcolor;
        }
        12% {
            box-shadow: .2em .2em 0 0 currentcolor;
        }
        25% {
            box-shadow: 0 .2em 0 0px currentcolor;
        }
        37% {
            box-shadow: -.2em .2em 0 0 currentcolor;
        }
        50% {
            box-shadow: -.2em 0 0 0 currentcolor;
        }
        62% {
            box-shadow: -.2em -.2em 0 0 currentcolor;
        }
        75% {
            box-shadow: 0px -.2em 0 0 currentcolor;
        }
        87% {
            box-shadow: .2em -.2em 0 0 currentcolor;
        }
    }

    //////////////////////////////////
    .loader-17 {
        position: relative;
        background-color: currentcolor;
        border-radius: 50%;
        &:after,
        &:before {
            content: "";
            position: absolute;
            width: .25em;
            height: .25em;
            border-radius: 50%;
            opacity: .8;
        }
        &:after {
            left: -.5em;
            top: -.25em;
            background-color: currentcolor;
            transform-origin: .75em 1em;
            animation: loader-17 1s linear infinite;
            opacity: .6;
        }
        &:before {
            left: -1.25em;
            top: -.75em;
            background-color: currentcolor;
            transform-origin: 1.5em 1em;
            animation: loader-17 2s linear infinite;
        }
    }

    @keyframes loader-17 {
        0% {
            transform: rotateZ(0deg) translate3d(0, 0, 0);
        }
        100% {
            transform: rotateZ(360deg) translate3d(0, 0, 0);
        }
    }

    //////////////////////////////////
    .loader-18 {
        position: relative;
        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            border-radius: 50%;
            border: .1em solid transparent;
            border-bottom-color: currentcolor;
            top: 0;
            left: 0;
            animation: 1s loader-18 linear infinite;
        }
        &:before {
            width: 1em;
            height: 1em;
        }
        &:after {
            width: .8em;
            height: .8em;
            top: .1em;
            left: .1em;
            animation-direction: reverse;
        }
    }

    @keyframes loader-18 {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    /////////////////////////////////////////
    .loader-19 {
        border-top: .2em solid currentcolor;
        border-right: .2em solid transparent;
        animation: loader-19 1s linear infinite;
        border-radius: 100%;
        position: relative;
    }

    @keyframes loader-19 {
        to {
            transform: rotate(360deg);
        }
    }

    ////////////////////////////////////////
    .loader-20 {
        background-color: transparent;
        box-shadow: inset 0px 0px 0px .1em currentcolor;
        border-radius: 50%;
        position: relative;
        &:after,
        &:before {
            position: absolute;
            content: "";
            background-color: currentcolor;
            top: .5em;
            left: .5em;
            height: .1em;
            transform-origin: left center;
        }
        &:after {
            width: .4em;
            animation: loader-20 2s linear infinite;
        }
        &:before {
            width: .3em;
            animation: loader-20 8s linear infinite;
        }
    }

    @keyframes loader-20 {
        0% {
            transform: rotate(0deg)
        }
        100% {
            transform: rotate(360deg)
        }
    }

    ////////////////////////////////////////
    .loader-21 {
        position: relative;
        &:before,
        &:after {
            position: absolute;
            content: "";
        }
        &:before {
            width: 80%;
            height: 80%;
            left: 10%;
            bottom: 10%;
            border-radius: 100% 100% 100% 0;
            box-shadow: 0px 0px 0px .1em currentcolor;
            animation: loader-21 1s linear infinite;
            transform: rotate(-46deg);
        }
        &:after {
            width: 1em;
            height: .3em;
            border-radius: 100%;
            left: 0;
            background-color: rgba(255, 255, 255, 0.2);
            bottom: -.2em;
            z-index: -1;
        }
    }

    @keyframes loader-21 {
        0% {
            top: 0;
        }
        50% {
            top: -5px;
        }
        100% {
            top: 0;
        }
    }

    ////////////////////////////////////////////////
    .loader-22 {
        border: .1em currentcolor solid;
        border-radius: 100%;
        position: relative;
        overflow: hidden;
        z-index: 1;
        &:after,
        &:before {
            position: absolute;
            content: "";
            background-color: currentcolor;
        }
        &:after {
            width: 50%;
            height: .1em;
            left: 50%;
            top: 50%;
            transform-origin: left center;
            animation: loader-22 2s linear infinite alternate;
        }
        &:before {
            width: 100%;
            height: 40%;
            left: 0;
            bottom: 0;
        }
    }

    @keyframes loader-22 {
        0% {
            transform: rotate(-160deg);
        }
        100% {
            transform: rotate(-20deg);
        }
    }

    /////////////////////////////////////////////
    .loader-23 {
        height: .5em;
        border: .1em currentcolor solid;
        border-radius: .1em;
        position: relative;
        animation: loader-23 5s linear infinite;
        &:after {
            width: .07em;
            height: 100%;
            background-color: currentcolor;
            border-radius: 0px .5em .5em 0px;
            position: absolute;
            content: "";
            top: 0;
            left: calc(100% + .1em);
        }
    }

    @keyframes loader-23 {
        0% {
            box-shadow: inset 0px 0px 0px currentcolor;
        }
        100% {
            box-shadow: inset 1em 0px 0px currentcolor;
        }
    }

    /////////////////////////////////////////////
    .loader-24 {
        width: .8em;
        height: 1em;
        border: .1em currentcolor solid;
        border-radius: 0px 0px .2em .2em;
        position: relative;
        &:after,
        &:before {
            position: absolute;
            content: "";
        }
        &:after {
            width: .2em;
            height: 50%;
            border: .1em currentcolor solid;
            border-left: none;
            border-radius: 0px .5em .5em 0px;
            left: calc(100% + .1em);
            top: .1em;
        }
        &:before {
            width: .1em;
            height: .3em;
            background-color: currentcolor;
            top: -.3em;
            left: .05em;
            box-shadow: .2em 0px 0px 0px currentcolor, .2em -.2em 0px 0px currentcolor, .4em 0px 0px 0px currentcolor;
            animation: loader-24 1s linear infinite alternate;
        }
    }

    @keyframes loader-24 {
        0% {
            height: 0px
        }
        100% {
            height: 6px;
        }
    }

    ////////////////////////////////////////
    .loader-25 {
        border: .1em currentcolor solid;
        position: relative;
        animation: loader-25-1 5s linear infinite;
        &:after {
            width: .2em;
            height: .2em;
            position: absolute;
            content: "";
            background-color: currentcolor;
            bottom: calc(100% + .2em);
            left: -.4em;
            animation: loader-25-2 1s ease-in-out infinite;
        }
    }

    @keyframes loader-25-1 {
        0% {
            box-shadow: inset 0 0 0 0 currentcolor;
        }
        100% {
            box-shadow: inset 0 -1em 0 0 currentcolor;
        }
    }

    @keyframes loader-25-2 {
        25% {
            left: calc(100% + .2em);
            bottom: calc(100% + .2em);
        }
        50% {
            left: calc(100% + .2em);
            bottom: -.4em;
        }
        75% {
            left: -.4em;
            bottom: -.4em;
        }
        100% {
            left: -.4em;
            bottom: calc(100% + .2em);
        }
    }

    ////////////////////////////////////////////
    .loader-26 {
        width: .5em;
        height: .5em;
        background-color: currentcolor;
        box-shadow: 1em 0px 0px currentcolor;
        border-radius: 50%;
        animation: loader-26 1s ease-in-out infinite alternate;
    }

    @keyframes loader-26 {
        0% {
            opacity: 0.1;
            transform: rotate(0deg) scale(0.5);
        }
        100% {
            opacity: 1;
            transform: rotate(360deg) scale(1.2);
        }
    }

    //////////////////////////////////////////////
    .loader-27 {
        box-shadow: inset 0 0 0 .1em currentcolor;
        border-radius: 50%;
        position: relative;
        margin-left: 1.2em;
        &:before {
            content: '';
            display: block;
            width: inherit;
            height: inherit;
            border-radius: 50%;
            position: absolute;
            right: 1.2em;
            top: 0;
            box-shadow: inset 0 0 0 .1em currentcolor;
        }
        &:after {
            border: .2em solid currentcolor;
            box-shadow: -1.2em 0 0 0 currentcolor;
            width: 0;
            height: 0;
            border-radius: 50%;
            left: 50%;
            top: 25%;
            position: absolute;
            content: "";
            animation: loader-27 2s linear infinite alternate;
        }
    }

    @keyframes loader-27 {
        0% {
            left: 0;
        }
        100% {
            left: .5em;
        }
    }

    /////////////////////////////////////////////////
    .loader-28 {
        position: relative;
        animation: 2s loader-28-1 infinite;
        &:before {
            content: '';
            display: block;
            width: inherit;
            height: inherit;
            border-radius: 80% 20%;
            border: .1em solid currentcolor;
            transform: rotate(45deg);
            border-width: .1em .05em .05em .1em;
        }
        &:after {
            content: '';
            display: block;
            width: .2em;
            height: .2em;
            position: absolute;
            top: .4em;
            left: 50%;
            border-radius: 50%;
            box-shadow: -.07em .07em 0 .1em currentcolor;
            animation: 2s loader-28-2 linear infinite;
        }
    }

    @keyframes loader-28-1 {
        0%,
        100% {
            transform: scaleY(1);
        }
        10% {
            transform: scaleY(0);
        }
        20% {
            transform: scaleY(1);
        }
    }

    @keyframes loader-28-2 {
        0%,
        100% {
            transform: translateX(0);
        }
        30% {
            transform: translateX(-100%);
        }
        50% {
            transform: transalteX(200%);
        }
    }

    /////////////////////////////////////////
    .loader-29 {
        border-radius: 50%;
        box-shadow: inset 0 0 0 .1em currentcolor, -.5em -.5em 0 -.4em currentcolor, 0 -.7em 0 -.4em currentcolor, .5em -.5em 0 -.4em currentcolor, -.5em .5em 0 -.4em currentcolor, 0 .7em 0 -.4em currentcolor, .5em .5em 0 -.4em currentcolor, -.7em 0 0 -.4em currentcolor, .7em 0 0 -.4em currentcolor;
        animation: 5s loader-29 linear infinite;
    }

    @keyframes loader-29 {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    ///////////////////////////////////////
    .loader-30 {
        border: .2em solid transparent;
        border-top-color: currentcolor;
        border-bottom-color: currentcolor;
        border-radius: 50%;
        position: relative;
        animation: 1s loader-30 linear infinite;
        &:before,
        &:after {
            content: '';
            display: block;
            width: 0;
            height: 0;
            position: absolute;
            border: .2em solid transparent;
            border-bottom-color: currentcolor;
        }
        &:before {
            transform: rotate(135deg);
            right: -.3em;
            top: -.05em;
        }
        &:after {
            transform: rotate(-45deg);
            left: -.3em;
            bottom: -.05em;
        }
    }

    @keyframes loader-30 {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }

    //////////////////////////////////////
    .loader-31 {
        box-shadow: 0 0 2em currentcolor;
        background-color: currentcolor;
        position: relative;
        border-radius: 50%;
        transform: rotateX(-60deg) perspective(1000px);
        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: inherit;
            height: inherit;
            border-radius: inherit;
            animation: 1s loader-31 ease-out infinite;
        }
        &:after {
            animation-delay: .4s;
        }
    }

    @keyframes loader-31 {
        0% {
            opacity: 1;
            transform: rotate(0deg);
            box-shadow: 0 0 0 -.5em currentcolor, 0 0 0 -.5em currentcolor, 0 0 0 -.5em currentcolor, 0 0 0 -.5em currentcolor, 0 0 0 -.5em currentcolor, 0 0 0 -.5em currentcolor, 0 0 0 -.5em currentcolor, 0 0 0 -.5em currentcolor;
        }
        100% {
            opacity: 0;
            transform: rotate(180deg);
            box-shadow: -1em -1em 0 -.35em currentcolor, 0 -1.5em 0 -.35em currentcolor, 1em -1em 0 -.35em currentcolor, -1.5em 0 0 -.35em currentcolor, 1.5em -0 0 -.35em currentcolor, -1em 1em 0 -.35em currentcolor, 0 1.5em 0 -.35em currentcolor, 1em 1em 0 -.35em currentcolor;
