<script>
    import "@fortawesome/fontawesome-free/css/all.css";
    import { createEventDispatcher } from "svelte";

    export let month = new Date().getMonth();
    export let year = new Date().getFullYear();

    const dispatch = createEventDispatcher();
    const today = new Date();
    const weekdays = [7, 6, 5, 4, 3, 2, 1];
    const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];

    let picked_date;
    let hours = "";
    let minutes = "";

    /**
     * @param {number} - The month number, 0 based.
     * @param {number} - The year, not zero based, required to account for leap years.
     * @return {Date[]} - List with date objects for each day of the month.
     */
    function month_days(month, year) {
        const date = new Date(year, month, 1);
        let days = [];
        while (date.getMonth() === month) {
            days.push(new Date(date));
            date.setDate(date.getDate() + 1);
        }
        return days;
    }

    function dec_month() {
        month -= 1;
        if (month < 0) {
            month = 11;
            year --;
        }
    }

    function inc_month() {
        month += 1;
        if (month > 11) {
            month = 0;
            year ++;
        }
    }

    function final_day_of_month(year, month) {
        return new Date(year, month + 1, 0);
    }

    function day_x_next_month(year, month, date) {
        month = month + 1;
        return new Date(year, month, date);
    }

    function is_same_date(date, cmp_date) {
        if (!date) {
            return false;
        }
        cmp_date = !!cmp_date ? cmp_date : today;
        return date.getFullYear() === cmp_date.getFullYear()
            && date.getMonth() === cmp_date.getMonth()
            && date.getDate() === cmp_date.getDate();
    }

    function prev_month(month) {
        return month === 0 ? 11 : month - 1;
    }

    function next_month(month) {
        return month === 11 ? 0 : month + 1;
    }

    function submit_date() {
        if (hours === "" || minutes === "" || !picked_date) {
            return;
        }
        picked_date.setHours(parseInt(hours), parseInt(minutes));
        dispatch("date", {date: picked_date});
    }

    function cancel() {
        dispatch("cancel");
    }

    let weeks = {};

    $: this_month_days = month_days(month, year);
    $: month_string = months[month % months.length];
    $: {
        weeks = {};
        this_month_days.forEach((day) => {
            const week_number = `${day.getWeek()}`;
            if (!Object.keys(weeks).includes(week_number)) {
                weeks[week_number] = [];
            }
            weeks[week_number].push(day);
        });

    }
</script>
<div class="datepicker">
    <div class="header">
        <div class="prev" on:click={dec_month}>
            <span class="chevron">&#8249;</span>
            <span class="month">
                {months[(month > 0 ? month - 1 : 11) % months.length].substring(0, 3)}
            </span>
        </div>
        <span class="current-month">{month_string} {year}</span>
        <div class="next" on:click={inc_month}>
            <span class="month">
                {months[(month + 1) % months.length].substring(0, 3)}
            </span>
            <span class="chevron">&#x203a;</span>
        </div>
    </div>
    <table>
        <thead>
            <tr>
                <th>#</th>
                <th>Mon</th>
                <th>Tue</th>
                <th>Wed</th>
                <th>Thu</th>
                <th>Fri</th>
                <th>Sat</th>
                <th>Sun</th>
            </tr>
        </thead>
        <tbody>
            {#each Object.keys(weeks) as week_number}
                <tr class="week">
                    {#if month !== 11 || week_number !== "1"}
                        <td class="week-number">{week_number}</td>
                        {#each weeks[week_number] as date}
                            {#if date.getDate() === 1}
                                {#each weekdays.slice(weeks[week_number].length) as i}
                                    <td
                                        class="date disabled"
                                        class:today={is_same_date(new Date(year, month, 1 - i))}
                                        class:picked={is_same_date(picked_date, new Date(year, month, 1 - i))}
                                        on:click={() => picked_date = new Date(year, month, 1 - i)}
                                    >
                                        {new Date(new Date().setDate(weeks[week_number][0].getDate() - i)).getDate()}
                                    </td>
                                {/each}
                            {/if}
                            <td
                                class="date"
                                class:picked={is_same_date(picked_date, date)}
                                class:today={is_same_date(date)}
                                on:click={() => picked_date = date}
                            >
                                {date.getDate()}
                            </td>
                            {#if date.getDate() === final_day_of_month(date.getFullYear(), date.getMonth()).getDate()}
                                {#each weekdays.slice(weeks[week_number].length) as _, i}
                                    <td
                                        class="date disabled"
                                        class:today={is_same_date(day_x_next_month(year, month, i + 1))}
                                        class:picked={is_same_date(picked_date, day_x_next_month(year, month, i + 1))}
                                        on:click={() => picked_date = day_x_next_month(year, month, i + 1)}
                                    >
                                        {i + 1}
                                    </td>
                                {/each}
                            {/if}
                        {/each}
                    {/if}
                </tr>
            {/each}
            {#if month === 11}
                <td class="week-number">1</td>
                {#each weeks["1"] as date}
                    <td
                        class="date"
                        class:picked={is_same_date(picked_date, date)}
                        class:today={is_same_date(date)}
                        on:click={() => picked_date = date}
                    >
                        {date.getDate()}
                    </td>
                    {#if date.getDate() === final_day_of_month(date.getFullYear(), date.getMonth()).getDate()}
                        {#each weekdays.slice(weeks["1"].length) as _, i}
                            <td
                                class="date disabled"
                                class:today={is_same_date(day_x_next_month(year, month, i + 1))}
                                class:picked={is_same_date(picked_date, day_x_next_month(year, month, i + 1))}
                                on:click={() => picked_date = day_x_next_month(year, month, i + 1)}
                            >
                                {i + 1}
                            </td>
                        {/each}
                    {/if}
                {/each}
            {/if}
        </tbody>
    </table>
    <div class="footer">
        <button class="footer-btn cancel" on:click={cancel}>&#xd7;</button>
        <div class="timepicker">
            <input type="text" class="numerical" placeholder="12" bind:value={hours}/>
            :
            <input type="text" class="numerical" placeholder="48" bind:value={minutes}/>
        </div>
        <button
            class="footer-btn submit"
            class:disabled={hours === "" || minutes === "" || !picked_date}
            on:click={submit_date}
        >
            &#x2713;
        </button>
    </div>
</div>

<style lang="scss">
    .datepicker {
        display: flex;
        flex-direction: column;
        width: 315px;
        background-color: white;
        box-shadow: 0 5px 10px 2px rgba(0, 0, 0, 0.3);

        .header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 5px;
            font-size: 18px;
            text-transform: uppercase;
            color: white;
            font-weight: bold;
            background-color: #0681ff;

            .next, .prev {
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: color .15s linear;
                font-size: 14px;
                width: 55px;
                padding: 0 10px;
                height: 40px;

                &:hover {
                    background-color: #0068d2;
                }

                .chevron {
                    margin-top: -4px;
                    font-size: 22px;
                }
            }

            .next {
                text-align: right;

                .month {
                    margin-right: 5px;
                }
            }

            .prev {
                text-align: left;

                .month {
                    margin-left: 5px;
                }
            }
        }

        table {
            width: 100%;

            * {
                text-align: center;
            }

            td {
                width: 35px;
                height: 35px;
                font-size: 16px;

                &:not(.week-number) {
                    cursor: pointer;

                    &.today {
                        background-color: #7dabca;
                    }

                    &:hover, &.picked {
                        background-color: #0681ff;
                        color: white;
                    }

                    &.disabled:not(.picked):not(.today) {
                        color: #AAA;
                    }
                }

                &.week-number {
                    font-weight: bold;
                }
            }
        }
    }

    .footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 5px;

        .timepicker {
            display: flex;
            align-items: center;
            justify-content: center;
            justify-self: center;

            * {
                font-size: 22px;
            }

            input.numerical {
                width: 25px;
                border: none;
                border-bottom: 1px solid #AAA;
                padding: 0 5px;
                text-align: center;
            }
        }

        .footer-btn {
            height: 28px;
            width: 28px;
            color: white;
            font-weight: bold;
            border: none;
            cursor: pointer;

            &.submit {
                background-color: #0681ff;

                &:hover {
                    background-color: #0068d2;
                }

                &.disabled {
                    cursor: default;
                    background-color: #797979;

                    &:hover {
                        background-color: #797979;
                    }
                }
            }

            &.cancel {
                background-color: #797979;

                &:hover {
                    background-color: #6b6b6b;
                }
            }
        }
    }
</style>