<script>
    import { createEventDispatcher } from "svelte";
    const dispatch = createEventDispatcher();

    export let ignored = false;

    function emit() {
        dispatch("toggle");
    }
</script>

<div class="switch-wrap">
    <label class="switch">
        <input type="checkbox" bind:checked={ignored} on:change={emit}>
        <span class="slider round"></span>
    </label>
    <span class:ignored={ignored} class:not-ignored={!ignored}>{ignored ? "" : "not"} ignored</span>
</div>

<style lang="scss">
    $ignored_color: #f32121;

    .switch-wrap {
        display: flex;
        align-items: center;

        & > span {
            color: white;
            text-transform: uppercase;
            width: 105px;
            font-size: 16px;

            &:first-child {
                text-align: right;
            }

            &:last-child {
                text-align: left;
            }

            &.ignored {
                color: $ignored_color;
            }

            &.not-ignored {
                color: #689dc0;
            }
        }

        .switch {
            position: relative;
            display: inline-block;
            width: 60px;
            height: 34px;
            margin: 0 5px;

            input {
                opacity: 0;
                width: 0;
                height: 0;

                &:checked + .slider {
                    background-color: $ignored_color;
                }

                &:focus + .slider {
                    box-shadow: 0 0 1px $ignored_color;
                }

                &:checked + .slider:before {
                    -webkit-transform: translateX(26px);
                    -ms-transform: translateX(26px);
                    transform: translateX(26px);
                }
            }

            .slider {
                position: absolute;
                cursor: pointer;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-color: #ccc;

                &:before {
                    position: absolute;
                    content: "";
                    height: 26px;
                    width: 26px;
                    left: 4px;
                    bottom: 4px;
                    background-color: white;
                }

                &.round {
                    border-radius: 34px;

                    &:before {
                        border-radius: 50%;
                    }
                }
            }
        }
    }
</style>