import { pad_int } from "./lib.js";

// Takes an ISO time and returns a string representing how
// long ago the date represents.
export function pretty_date(time) {
    const date = new Date(time);
    const diff = (((new Date()).getTime() - date.getTime()) / 1000);
    const day_diff = Math.ceil(diff / 86400);

    if (isNaN(day_diff) || day_diff < 0) {
        return;
    }

    return day_diff == 0 && (
    diff < 60 && "just now"
        || diff < 120 && "1 minute ago"
        || diff < 3600 && Math.floor(diff / 60) + " minutes ago"
        || diff < 7200 && "1 hour ago"
        || diff < 86400 && Math.floor(diff / 3600) + " hours ago")
        || day_diff == 1 && "1 day ago"
        || day_diff + " days ago";
}

const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
export function human_date(date) {
    const YYYY = date.getFullYear();
    const MM = months[date.getMonth()];
    const dd = `${date.getDate()}${date_ordinal(date.getDate())}`;
    const HH = pad_int(date.getHours());
    const mm = pad_int(date.getMinutes());
    return `${MM} ${dd} ${YYYY} ${HH}:${mm}`;
}

function date_ordinal(d) {
    if (d > 3 && d < 21) {
        return "th";
    }
    switch (d % 10) {
        case 1:  return "st";
        case 2:  return "nd";
        case 3:  return "rd";
        default: return "th";
    }
}