<script>
    import { onMount } from "svelte";
    import { axle_query } from "../stores.js"
    import config from "../const.js";
    import IgnoreToggle from "./IgnoreToggle.svelte";
    import DatePicker from "./DatePicker.svelte";
    import Modal from "./Modal.svelte";
    import { pad_int } from "../lib.js";

    export let counters = [];
    export let exclude = [];
    export let sorting = {};
    export let threshold = 25;
    export let aliases = {};
    export let ascending = false;
    export let sortby = sorting[0];

    let show_modal = false;
    let page_index = 0;
    let shoveling_date;
    let selected_axle_counter;
    let maxlen = Math.max(Math.floor((window.innerHeight - 400) / 36) + 1, 5);

    $: filtered_reports = counters.filter((report, index) => {
        return (report.axle_counter.toLowerCase().includes($axle_query.toLowerCase())
            || `${report.number_of_crossings}`.includes($axle_query)
            || $axle_query.toLowerCase() === "ignored" && report.ignored
            || $axle_query.toLowerCase() === "not ignored" && !report.ignored
            || report.crossing_time.includes($axle_query)
            || $axle_query === "");
    }).sort(sorting[sortby][ascending ? "asc" : "desc"]);

    $: last_page = Math.floor(filtered_reports.length / maxlen);
    $: page_index = page_index > last_page ? last_page : page_index;

    function next() {
        if (page_index >= last_page) {
            return;
        }
        page_index = page_index >= last_page ? last_page : page_index + 1;
    }

    function prev() {
        if (page_index <= 0) {
            return;
        }
        page_index = page_index <= 0 ? 0 : page_index - 1;
    }

    function shoveling_submit() {
        fetch(`${config.api_url}/shoveling/`, {
            method: "POST",
            body: JSON.stringify({
                id: selected_axle_counter.id,
                shoveling_timestamp: shoveling_date.toISOString(),
            }),
        }).then(() => {
            console.log("Success")
        }).catch((error) => {
            console.error(error)
        }).finally(() => {
            show_modal = false;
            shoveling_date = null;
            selected_axle_counter = null;
        });
    }

    function set_sorting(key) {
        if (key === sortby) {
            ascending = !ascending;
            return;
        }
        ascending = true;
        sortby = key;
    }

    function shoveling_cancel() {
        show_modal = false;
        shoveling_date = null;
        selected_axle_counter = null;
    }

    function toggle_counter_ignore(counter) {
        counter.ignored = !counter.ignored;
        const url = `${config.api_url}/ignored_axle_counters`;
        const req = fetch(url, { method: counter.ignored ? "POST" : "DELETE", body: {name: counter.axle_counter} });
        req.catch(() => {
            console.log("ERROR IN TOGGLE");
            counter.ignored = !counter.ignore;
        });
    }

    function register_shoveling(counter) {
        selected_axle_counter = counter;
        show_modal = true;
    }

    window.onresize = function(event) {
        maxlen = Math.max(Math.floor((window.innerHeight - 400) / 36) + 1, 5);
        page_index = Math.min(page_index, last_page);
    };
</script>

<table>
    <thead>
        <tr>
            {#each Object.keys(counters[0]) as key}
                {#if !exclude.includes(key)}
                    <th
                        class:sortable={Object.keys(sorting).includes(key)}
                        on:click={() => set_sorting(key)}
                    >
                        {#if Object.keys(sorting).includes(key)}
                            {#if key === sortby}
                                {#if ascending}
                                    <i class="fas fa-sort-up"></i>
                                {:else}
                                    <i class="fas fa-sort-down"></i>
                                {/if}
                            {:else}
                                <i class="fas fa-sort"></i>
                            {/if}
                        {/if}
                        {#if Object.keys(aliases).includes(key)}
                            {aliases[key]}
                        {:else}
                            {key}
                        {/if}
                    </th>
                {/if}
            {/each}
            <th
                style="width: 175px;"
                class:sortable={Object.keys(sorting).includes("days_since_last_crossing")}
                on:click={() => set_sorting("days_since_last_crossing")}
            >
                {#if "days_since_last_crossing" === sortby}
                    {#if ascending}
                        <i class="fas fa-sort-up"></i>
                    {:else}
                        <i class="fas fa-sort-down"></i>
                    {/if}
                {:else}
                    <i class="fas fa-sort"></i>
                {/if}
                last crossing
            </th>
            <th
                style="width: 175px;"
                class:sortable={Object.keys(sorting).includes("ignored")}
                on:click={() => set_sorting("ignored")}
            >
                {#if "ignored" === sortby}
                    {#if ascending}
                        <i class="fas fa-sort-up"></i>
                    {:else}
                        <i class="fas fa-sort-down"></i>
                    {/if}
                {:else}
                    <i class="fas fa-sort"></i>
                {/if}
                ignored
            </th>
            <th style="width: 175px;">ignore</th>
            <th style="width: 250px;">shoveling</th>
        </tr>
    </thead>
    <tbody>
        {#each filtered_reports as counter, index}
            {#if index >= page_index * maxlen && index <= page_index * maxlen + maxlen}
                <tr>
                    {#each Object.keys(counter) as key}
                        {#if !exclude.includes(key)}
                            <td>{counter[key]}</td>
                        {/if}
                    {/each}
                    <td>
                        {#if !counter.days_since_last_crossing}
                            <div class="no-last-crossing">
                                NOT CROSSED
                            </div>
                        {:else}
                            {#if counter.days_since_last_crossing >= threshold}
                                <div class="no-last-crossing">
                                    {counter.days_since_last_crossing} days ago
                                </div>
                            {:else}
                                {counter.days_since_last_crossing} days ago
                            {/if}
                        {/if}
                    </td>
                    <td>
                        <IgnoreToggle
                            ignored={counter.ignored}
                            on:toggle={() => toggle_counter_ignore(counter)}
                        />
                    </td>
                    <td>
                        <button
                            class="shoveling-btn"
                            on:click={() => register_shoveling(counter)}>
                            <i class="fas fa-clipboard-check" />
                            <span>Register shoveling</span>
                        </button>
                    </td>
                </tr>
            {/if}
        {/each}
    </tbody>
</table>

<div class="table-nav">
    <div class="center-wrap">
        <button class:disabled={page_index <= 0} on:click={prev}>
            prev
        </button>
        <span>Page {page_index + 1} of {last_page + 1}</span>
        <button class:disabled={page_index >= last_page} on:click={next}>
            next
        </button>
    </div>
</div>

<Modal
    title={"register shoveling"}
    show={show_modal}
    on:hide={() => (show_modal = false)}
>
    <DatePicker on:date={(e) => shoveling_date = e.detail.date} on:cancel={() => show_modal = false}/>
    <button
        class="submit-shoveling"
        class:disabled={!shoveling_date}
        on:click={shoveling_submit}
    >
        {shoveling_date ? `${config.months[shoveling_date.getMonth()]} ${pad_int(shoveling_date.getDate())} ${shoveling_date.getFullYear()} ${pad_int(shoveling_date.getHours())}:${pad_int(shoveling_date.getMinutes())}` : "Submit"}
    </button>
</Modal>

<style lang="scss">
    table {
        width: 100%;
        border-spacing: unset;

        th, td {
            min-width: 50px;

            &:first-child {
                min-width: 400px;
            }

            &:last-child {
                text-align: center;
            }
        }

        thead {
            text-transform: uppercase;
            text-align: left;

            th.sortable {
                cursor: pointer;

                i {
                    color: #0068d2;
                }
            }
        }

        tbody {
            text-transform: initial;

            tr {
                background-color: white;

                &:nth-child(odd) {
                    background-color: #F3F3F3;
                }

                td {
                    font-size: 20px;
                    height: 34px;

                    &:last-child {
                        display: flex;
                        justify-content: center;
                    }

                    .shoveling-btn {
                        display: flex;
                        justify-content: center;
                        height: 28px;
                        text-transform: uppercase;
                        margin: 3px 0;
                        border: none;
                        color: white;
                        background-color: inherit;
                        cursor: pointer;
                        padding: 0;

                        &:hover {
                            i {
                                font-size: 18px;
                                background-color: #0068d2
                            }

                            span {
                                color: #0068d2;
                                border-color: #0068d2;
                            }
                        }

                        i {
                            color: white;
                            background-color: #0681ff;
                            font-size: 18px;
                            width: 28px;
                            height: 28px;
                            line-height: 28px;
                        }

                        span {
                            font-size: 14px;
                            padding: 0 5px;
                            line-height: 28px;
                            color: #0681ff;
                            border: 1px solid #0681ff;
                            border-left: none;
                        }
                    }

                    .no-last-crossing {
                        font-weight: bold;
                        color: #f32121;
                    }
                }
            }
        }
    }

    .table-nav {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: flex-end;
        flex: 1;

        .center-wrap {
            display: flex;
            justify-content: center;
            align-items: center;

            button {
                cursor: pointer;
                padding: 3px;
                width: 80px;
                font-size: 16px;
                text-transform: uppercase;
                border: none;
                background-color: #0681ff;
                color: white;

                &:not(.disabled):hover {
                    background-color: #0068d2;
                }

                &.disabled {
                    cursor: default;
                    color: black;
                    background-color: #888;
                }
            }

            span {
                font-size: 20px;
                margin: 0 20px;
            }
        }
    }

    .submit-shoveling {
        width: 100%;
        margin-top: 15px;
        background-color: #0681ff;
        color: white;
        border: none;
        font-size: 22px;
        padding: 5px;

        &.disabled {
            background-color: #888;
        }

        &:not(.disabled) {
            cursor: pointer;

            &:hover {
                background-color: #0068d2;
            }
        }
    }
</style>
